import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Form, Button, message, Radio, Spin, Input, Select, Cascader, Upload, Icon } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;
const { TextArea } = Input;
import InvestItem from '../FormItem/investItem.jsx'
const provinceList = {
    "110000": "北京市",
    "120000": "天津市",
    "130000": "河北省",
    "140000": "山西省",
    "150000": "内蒙古自治区",
    "210000": "辽宁省",
    "220000": "吉林省",
    "230000": "黑龙江省",
    "310000": "上海市",
    "320000": "江苏省",
    "330000": "浙江省",
    "340000": "安徽省",
    "350000": "福建省",
    "360000": "江西省",
    "370000": "山东省",
    "410000": "河南省",
    "420000": "湖北省",
    "430000": "湖南省",
    "440000": "广东省",
    "450000": "广西壮族自治区",
    "460000": "海南省",
    "500000": "重庆市",
    "510000": "四川省",
    "520000": "贵州省",
    "530000": "云南省",
    "540000": "西藏自治区",
    "610000": "陕西省",
    "620000": "甘肃省",
    "630000": "青海省",
    "640000": "宁夏回族自治区",
    "650000": "新疆维吾尔自治区",
    "710000": "台湾省",
    "810000": "香港特别行政区",
    "820000": "澳门特别行政区"
}
export class PurchaseForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            disabled: props.actionType == "detail" ? true : false,
            loading: false,
            "assetsType": {
                "debts":[],
                "stock": [],
                "assetsAcquisition": []
            },
            "industryList": [],
            "provinceList": {},
            "key": "",      //编号
            "title": "",    //标题
            "category": "",         //类别
            "type": [],    //标题
            "region": [],
            "invest": {     //单笔投资区间
                "min": "",
                "max": ""
            },
            "needCost": "",        //前期费用
            "detail": "",   //详情
            "accessory": {        //附件
                "name": "",
                "link": ""
            },
            'fileList': []
        };
        ['handleSubmit', 'resetForm', 'uploadChange'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        let that = this;
        this.setState({loading: true});
        adminAjax({
            url: "/delegateFund/getInfoDetail",
            method: "post",
            data: {id: that.props.infoID ? that.props.infoID : ""},
            callback: function(data) {
                let newData = data;
                that.setState({
                    loading: false,
                    "assetsType": {
                        "debts":newData.debts,
                        "stock": newData.stock,
                        "assetsAcquisition": newData.assetsAcquisition
                    },
                    "industryList": newData.industryList,
                    "key": newData.key,      //编号
                    "title": newData.title,    //标题
                    "category": newData.category,         //类别
                    "type": newData.investmentType.split(','),        //投资类型
                    "industry": newData.investmentIndustry.split(','),    //投资行业
                    "region": newData.region,     //投资地区
                    "invest": {     //单笔投资区间
                        "min": newData.investMin,
                        "max": newData.investMax
                    },
                    "needCost": newData.needCost.toString(),        //前期费用
                    "detail": newData.detail,   //详情
                    "accessory": {        //附件
                        "name": newData.accessoryName,
                        "link": newData.accessoryLink
                    },
                    fileList: [{
                        uid: "random",
                        name: newData.accessorName,
                        url: newData.accessoryLink,
                        status: "done",
                        response: 'Server Error 500'
                    }]
                })
            }
        })
    }

    investCheck(rule, value, callback) {
        if(!value.min|| !value.max) {
            callback('请输入单笔投资区间!');
        } else {
            if(value.max < value.min) {
                callback("投资上限必须大于下限!")
            }
            callback();
            return;
        }
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(!err) {
                let params = {
                    title: values.title,
                    category: values.category,
                    investmentType: values.type.join(","),
                    investmentIndustry: values.industry.join(","),
                    region: values.region,
                    investMin: values.invest.min,
                    investMax: values.invest.max,
                    needCost: values.needCost,
                    detail: values.detail
                }
                adminAjax({
                    url: "/delegateFund/edit",
                    method: "post",
                    data: this.props.actionType == "edit" ? Object.assign(params, {id: this.props.infoID}, {accessoryName: this.state.accessory.name, accessoryLink: this.state.accessory.link}) : Object.assign(params, {accessoryName: this.state.accessory.name, accessoryLink: this.state.accessory.link}),
                    callback: function(data) {
                        message.success("保存成功!", 1, function() {
                            hashHistory.push("/delegateFund")
                        });
                    }
                });
            }
        })
    }

    return() {
        history.back();
    }

    resetForm() {
        this.props.form.resetFields();
    }

    uploadChange(info) {
        let that = this;
        let file = info.file;
        let fileList = info.fileList;
        this.setState({loading: true});
        if(fileList.length > 0) {
            if(fileList.length > 1) {
                fileList = [file];
            }
            if(fileList[0].status == "error") {
                fileList = [];
                message.error("上传图片失败!");
            }
            if(fileList[0].status == "done") {
                fileList[0].url = `${fileList[0].response.url}`;
                this.setState({
                    accessory: {
                        name: fileList[0].name,
                        link: fileList[0].url
                    }
                })
            }
        } else {
            this.setState({
                accessory: {
                    name: "",
                    link: ""
                }
            })
        }

        this.setState({fileList, loading: false});
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 3 },
            wrapperCol: { span: 14 }
        };
        var regionOps = [];
        for(var key in this.state.provinceList) {
            regionOps.push({
                key,
                value: this.state.provinceList[key]
            });
        }
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="资金标题"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("title", {
                            rules: [{required: true, message: "请输入标题!"}],
                            initialValue: this.state.title
                        })(
                            <Input disabled={this.state.disabled} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="资金类别"
                    >
                        {getFieldDecorator("category", {
                            initialValue: "3"
                        })(
                            <Select disabled>
                                <Option value="1">债权</Option>
                                <Option value="2">股权</Option>
                                <Option value="3">资金收购</Option>
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="投资类型"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("type", {
                            rules: [{
                                required: true,
                                message: "请选择投资类型!"
                            }],
                            initialValue: this.state.type
                        })(
                            <Select 
                                disabled={this.state.disabled}
                                placeholder="请选择类型"
                                mode="multiple"
                            >
                                {
                                    this.state.assetsType.assetsAcquisition.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="投资地区"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("region", {
                            rules: [{
                                required: true,
                                message: "请选择投资地区!"
                            }],
                            initialValue: this.state.region
                        })(
                            <Select
                                className="multiSelect"
                                placeholder="请选择地区"
                                mode="multiple"
                                disabled={this.state.disabled}
                            >
                                {regionOps.map(item =>
                                    <Option key={item.key} value={item.key.toString()}>{item.value}</Option>
                                )}
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="单笔投资区间"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("invest", {
                            rules: [{
                                validator: this.investCheck
                            }],
                            initialValue: {
                                min: this.state.invest.min,
                                max: this.state.invest.max
                            }
                        })(<InvestItem disabled={this.state.disabled} />)}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="前期费用"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("needCost", {
                            rules: [{
                                required: true,
                                message: "请设置前期费用!"
                            }],
                            initialValue: this.state.needCost
                        })(
                            <RadioGroup disabled={this.state.disabled} >
                                <Radio value="1">有(评估/担保等)</Radio>
                                <Radio value="0">没有</Radio>
                            </RadioGroup>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="资金详情"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("detail", {
                            rules: [{required: true, message: "请输入资金详情!"}],
                            initialValue: this.state.detail
                        })(
                            <TextArea disabled={this.state.disabled} autosize={{ minRows: 3 }} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="附件"
                    >
                        <Upload
                            name="file"
                            action="/admin/upload"
                            fileList={this.state.fileList}
                            onChange={this.uploadChange}
                        >
                            <Button>
                                <Icon type="upload" />上传文件
                            </Button>
                        </Upload>
                    </FormItem>
                    <FormItem wrapperCol={{ span: 8, offset: 3 }}>
                        {
                            !this.state.disabled ? <Button type="primary" onClick={this.handleSubmit} style={{"marginRight": "20px"}}>立即发布</Button> : ""
                        }
                        {
                            !this.state.disabled ? <Button type="danger" onClick={this.resetForm} style={{"marginRight": "20px"}}>重置</Button> : ""
                        }
                        <Button icon="rollback" onClick={this.return}>返回</Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedPurchaseForm = Form.create({})(PurchaseForm);

module.exports = WrappedPurchaseForm;


